.myPosts {
	display: flex;
	flex-direction: column;
	max-width: 100%;
	max-height: 80vh;
	overflow: auto;

	.post {
		display: flex;
		max-width: 100%;
		margin-bottom: 1rem;
		background-color: rgba(0, 0, 0, .6);
		border-radius: 1rem;
		outline: none;

		.postText {
			flex-basis: 100%;
			border: 1px solid white;
			border-radius: 1rem;
			outline: none;
			padding: 0.5rem;
			width: 100%;

			.postTextLabel {
				max-width: 100%;
			}
		}

		.userInfo {
			display: flex;
			flex-direction: column;
			text-align: center;

			.userInfoPhoto {
				max-width: 4rem;
				border-radius: 1rem;
				outline: none;
			}

			.userInfoText {
				color: red;
			}
		}
	}

	.post:last-child {
		margin-bottom: 0;
	}
}

.myPostsWrapper {
	display: flex;
	flex-direction: column;
	width: 50%;
	border-radius: 1rem;
	background-color: rgba(0, 0, 0, .5);
	margin-left: 0.5rem;
	padding: 0 0.5rem 0 1rem;

	.myPostForm {
		display: flex;
		flex-direction: column;
		text-align: center;
		padding: 0.5rem;

		.formInputMinimum {
			font-size: 0.811rem;
			align-self: flex-start;
			margin-left: 3rem;
		}

		.formInput {
			padding-bottom: 0.5rem;

			textarea {
				margin: 0 auto;
				border-radius: 0.5rem;
				outline: none;
				padding: 0.2rem;
				width: 80%;
				font-weight: bold;
			}

			textarea:focus {
				border: 1px solid green;
			}
		}

		.formButton {
			border-radius: 0.3rem;
			border: 1px solid green;
			outline: none;
			background-color: rgba(0, 0, 0, .5);
			color: white;
		}

		.formButton:disabled {
			border: 1px solid darkgrey;
			color: darkgrey;
		}
	}
}


@media screen and (max-width: 920px) {

	.myPostsWrapper {
		margin: 0.5rem 0 0 0;
		width: 100%;
		padding: 0;
	}
}

@media screen and (max-width: 600px) {
	.myPostsWrapper {
		flex-direction: column;
		font-size: 90%;
		width: 100%;
		padding: 0;
		margin-top: 1rem;

		.posts {
			width: 100%;
			padding: 0;
			margin: 0;
		}
	}
}
